.linksContainer {
  margin-top: 80px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;

  h4 {
    color: #fff;
    margin-top: 0;
    font-family: "Raleway", sans-serif;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.1;
    font-size: 18px;
  }

  .links {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;

    a {
      width: 50px;
      height: 50px;
      text-align: center;
      background: #555;
      margin: 0 10px;
    }
  }

}